h1 {
  font-family: 'Righteous', cursive;
  font-size: 150px;
  /* color: rgb(250, 3, 3); */
  color: whitesmoke;
}
.header {
    background-color: #2c323c;
    color: white;
    padding-top: 25px;
    padding-bottom: 25px;
    min-height: 100px;
    border-bottom: 5px solid black;
  }
  
  .logo-button {
   width: 100%;
    padding: 0;
    background-color: black;
    border: 1px solid black;
  }
  
  .logo-button:hover{
    background-color: black;
    border: 1px solid black;
  }
  
  #popover-positioned-left {
    max-width: 200px;
  }

  .heart {
    font-size: 150px;
    text-align: center;
    color: #a50810;
    -webkit-animation: beat .45s infinite alternate;
            animation: beat .45s infinite alternate;
    -webkit-transform-origin: center;
            transform-origin: center;
  }
  
  /* Heart beat animation */
  @-webkit-keyframes beat{
    to { -webkit-transform: scale(1.4); transform: scale(1.4); }
  }
  @keyframes beat{
    to { -webkit-transform: scale(1.4); transform: scale(1.4); }
  }


.btn-score {
    position: absolute;
    left: -50px;
    top: 125px;
    margin-top: -110px;
    padding-bottom: 15px;
}

.btn-score:hover {
    cursor: default;
}

#incorrect {
    -webkit-animation: bounce 0.82s linear infinite;
            animation: bounce 0.82s linear infinite;
  }
  
@-webkit-keyframes bounce{
    0%, 20%, 50%, 80%, 100% {
        -webkit-transform: translateY(0);
                transform: translateY(0);
    }
	40% {
        -webkit-transform: translateY(-12px);
                transform: translateY(-12px);
    }
	60% {
        -webkit-transform: translateY(-6px);
                transform: translateY(-6px);
    }
}
  
@keyframes bounce{
    0%, 20%, 50%, 80%, 100% {
        -webkit-transform: translateY(0);
                transform: translateY(0);
    }
	40% {
        -webkit-transform: translateY(-12px);
                transform: translateY(-12px);
    }
	60% {
        -webkit-transform: translateY(-6px);
                transform: translateY(-6px);
    }
}

@media only screen and (max-width: 1350px) {
    .btn-score {
        margin-top: 0px;
        position: static;
        border-radius: 0px;
    }
}

.container {
  display: flex;
  flex-wrap: wrap;
  padding-bottom: 15px;
  width: 75%;
  position: relative;
}

.btn-link {
top: 50%;
left: 5%;
}

.incorrect {
  -webkit-animation: shake 0.82s;
          animation: shake 0.82s;
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
  -webkit-perspective: 1000px;
          perspective: 1000px;
}


@-webkit-keyframes shake {
    10%, 90% {
      -webkit-transform: translate3d(-10px, 0, 0);
              transform: translate3d(-10px, 0, 0);
    }
    20%, 80% {
      -webkit-transform: translate3d(10px, 0, 0);
              transform: translate3d(10px, 0, 0);
    }
    30%, 50%, 70% {
      -webkit-transform: translate3d(-20px, 0, 0);
              transform: translate3d(-20px, 0, 0);
    }
    40%, 60% {
      -webkit-transform: translate3d(20px, 0, 0);
              transform: translate3d(20px, 0, 0);
    }
  }


@keyframes shake {
    10%, 90% {
      -webkit-transform: translate3d(-10px, 0, 0);
              transform: translate3d(-10px, 0, 0);
    }
    20%, 80% {
      -webkit-transform: translate3d(10px, 0, 0);
              transform: translate3d(10px, 0, 0);
    }
    30%, 50%, 70% {
      -webkit-transform: translate3d(-20px, 0, 0);
              transform: translate3d(-20px, 0, 0);
    }
    40%, 60% {
      -webkit-transform: translate3d(20px, 0, 0);
              transform: translate3d(20px, 0, 0);
    }
  }
  
  .incorrect img{
    display:none;
  }
  
  @media only screen and (max-width: 1300px) {
    .gif {
      display: none;
    }
  }
a,a:hover {
    color: white;
}

table {
    height: 100px;
    background-color: #2c323c;
    border-top: 5px solid black;
    width: 100%;
    color: white;
}

.spacer {
    margin-bottom: 10px;
}

.fa-react {
    margin-right: 5px;
}

.fa-github {
    margin-left: 5px;
}

.click-item {
    height: 170px;
    width: 170px;
    margin: 8px 25px;
    border: 5px solid #f1f1f1;
    border-radius: 3px;
    cursor: pointer;
    box-shadow: 0 3px 6px rgba(0,0,0,.16), 0 3px 6px rgba(0,0,0,.23);
}

.neutral {
    background-color: pink;
}

.click-item:hover {
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
    box-shadow: 0 14px 28px rgba(0,0,0,.25), 0 10px 10px rgba(0,0,0,.22);
    -webkit-transition: .1s;
    transition: .1s;
}

.card-image {
    max-height: 160px;
    max-width: 160px;
}
